import React from 'react';

const SearchBox = (props) => {
    return(
        <div className='col col-md-5'>
            <input 
            className="form-control" 
            value={props.value}
            onChange={(event)=> props.setSearchValue(event.target.value)}
            placeholder="Type to search .."
            ></input>
        </div>
    );
};

export default SearchBox;
